import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import styled from "styled-components";
import axios from "axios";
import { useAuth } from "../context/AuthContext";

const LoginBox = () => {
  const { setToken, setLoginType } = useAuth(); // Context setToken 
  const [activeTab, setActiveTab] = useState(0);
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [errorMessage, setErrorMessage] = useState("");
  const navigate = useNavigate();

  const handleTabClick = (index) => {
    setActiveTab(index);
    setLoginType(index === 0 ? "BUYER" : "SELLER");
    setUsername("");
    setPassword("");
    setErrorMessage("");
  };

  const handleSubmit = (event) => {
    event.preventDefault(); //   

    const AuthData = {
      username,
      password,
      login_type: activeTab === 0 ? "BUYER" : "SELLER",
    };

    axios
      .post("https://openmarket.weniv.co.kr/accounts/login/", AuthData) //  API 
      .then((response) => {
        console.log(" :", response.data); //     
        localStorage.setItem("token", response.data.token);
        setToken(response.data.token);
        localStorage.setItem("username", username);
        localStorage.setItem("loginType", activeTab === 0 ? "BUYER" : "SELLER");
        navigate("/");
      })
      .catch((error) => {
        if (error.response) {
          setErrorMessage("    .");
        }
      });
  };

  return (
    <>
      <TabContainer>
        <Tab active={activeTab === 0} onClick={() => handleTabClick(0)}>
           
        </Tab>
        <Tab active={activeTab === 1} onClick={() => handleTabClick(1)}>
           
        </Tab>
      </TabContainer>
      <LoginContainer>
        <FormContainer>
          {activeTab === 0 ? (
            <Form onSubmit={handleSubmit}>
              <Input
                type="text"
                placeholder=""
                value={username}
                onChange={(e) => setUsername(e.target.value)}
                required
              />
              <Input
                type="password"
                placeholder=""
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
              />
              <ErrorMessage marginTop={errorMessage ? "26px" : ""}>
                {errorMessage}
              </ErrorMessage>
              <Button type="submit"></Button>
            </Form>
          ) : (
            <Form onSubmit={handleSubmit}>
              <Input
                type="text"
                placeholder=""
                value={username}
                onChange={(e) => setUsername(e.target.value)}
                required
              />
              <Input
                type="password"
                placeholder=""
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
              />
              <ErrorMessage marginTop={errorMessage ? "26px" : ""}>
                {errorMessage}
              </ErrorMessage>
              <Button type="submit"></Button>
            </Form>
          )}
        </FormContainer>
      </LoginContainer>
    </>
  );
};

const LoginContainer = styled.div`
  margin: 0 auto;
  /* margin-top: -7px; */
  padding-top: 44px;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 550px;
  border-radius: 0px 0px 10px 10px;
  border: 1px solid var(--color-orange);
  border-top: 0px;
  overflow: hidden;
  position: relative;
  z-index: 0;
`;

const TabContainer = styled.div`
  display: flex;
  /* overflow: hidden; */
  width: 550px;
  margin: 0 auto;
`;
const Tab = styled.div`
  width: 50%;
  border-top: 1px solid var(--color-orange);
  border-left: 1px solid var(--color-orange);
  border-right: 1px solid var(--color-orange);
  border-bottom: ${(props) => (props.active ? "null" : "1px solid #EC7E62")};
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  padding: 20px 70px 38px 70px;
  height: 60px;
  font-size: 18px;
  font-weight: 600;
  cursor: pointer;
  background-color: ${(props) => (props.active ? "white" : "#EDD0C2")};
  /* z-index: ${(props) => (props.active ? "10" : "0")}; */
  &:last-child {
    margin-right: 0;
  }
`;

const FormContainer = styled.div`
  padding-left: 35px;
  padding-right: 35px;
`;

const Form = styled.form`
  display: flex;
  flex-direction: column;
`;

const Input = styled.input`
  width: 480px;
  height: 60px;
  border-bottom: solid 1px var(--color-orange);
  margin-bottom: 6px;
`;

const Button = styled.button`
  background-color: var(--color-maroon);
  width: 480px;
  height: 60px;
  border-radius: 10px;
  font-size: 18px;
  color: white;
  margin-top: 30px;
  margin-bottom: 36px;
  font-weight: 700;
`;

const ErrorMessage = styled.p`
  font-size: 16px;
  margin-top: ${(props) => props.marginTop};
  color: red;
`;

export default LoginBox;
